.banner {
  width: 100vw;
  height: 104vh;
  padding: 10px 2%;
  background-image: url("../../public/images/banner_main.jpg");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  position: relative;
}

@media (max-width: 780px) {
  .banner {
    background-size: cover;
  }
}
@media (max-width: 500px) {
  .banner {
    height: 100vh !important;
  }
}